import * as React from "react";
import {IRouterContext} from "../../dataTypes/IRouterContext";
import {RouteTransition} from "react-router-transition";
import _ = require("lodash");
declare let $;


export default class AppFrame extends React.Component<any, any> {

    static contextTypes = {
        router: React.PropTypes.any,
    };

    context: IRouterContext;

    render() {
        return (
            <div className="app-frame">

                <div className="mui-appbar">
                    <table width="100%">
                        <tr>
                            <td className="mui--appbar-height app-title">
                                <h3>Super Smart</h3>
                            </td>
                        </tr>
                    </table>
                </div>

                <RouteTransition
                    className="app-wrapper"
                    pathname={this.props.location.pathname}
                    atEnter={{ translateX: 100,fixed:1, top : 100 }}
                    atLeave={{ translateX: -100, fixed:1, top : 0}}
                    atActive={{ translateX: 0, fixed:0 , top : 0 }}
                    mapStyles={styles => {
                        // console.log(styles);
                        return ({ left: `calc(${styles.translateX}%)`,
                                  height : 'calc(100%)' ,
                                  position: 'relative',
                                  top: styles.top != 0 ? 'calc(-100%)' : '0'
                        })
                    }} runOnMount={false}>

                    {this.props.children}


                </RouteTransition>


                <div className="mui-appbar app-nav">

                    <button className="mui-btn" onClick={() =>{this.context.router.push('/dashboard/welcome')}}>
                        <span className="glyphicon glyphicon-home"/>
                    </button>

                    <button className="mui-btn" onClick={() =>{this.context.router.push('/dashboard/find')}}>
                        <span className="glyphicon glyphicon-search"/>
                    </button>

                    <button className="mui-btn" onClick={() =>{this.context.router.push('/dashboard/navigator')}}>
                        <span className="glyphicon glyphicon-move"/>
                    </button>

                    <button className="mui-btn" onClick={() =>{this.context.router.push('/dashboard/deals')}}>
                        <span className="glyphicon glyphicon-fire"/>
                    </button>

                </div>
            </div>
        );
    }
}



