import * as React from "react";
import {IRouterContext} from "../../dataTypes/IRouterContext";
import {Store, Item} from "../../store/store";
import _ = require("lodash");

declare let $;

export default class Navigator extends React.Component<any, any> {

    private targetImgSrc = '/assets/target.png';

    static contextTypes = {
        router: React.PropTypes.any,
    };

    context: IRouterContext;

    private _searchValue = '';

    componentWillMount() {
        this._searchValue = this.props.location.query['q'];
    }

    componentDidMount() {
        // this.drawLine();
        this.canvasEl = document.getElementsByClassName("route-canvas")[0];
        this.ctx = this.canvasEl.getContext("2d");

        $('.map-canvas').on('click', (e) => {
            var clickedX = ($('.map-canvas').scrollLeft()) + e.pageX + 70;
            var clickedY = ($('.map-canvas').scrollTop() ) + e.pageY - 25;
            console.log(clickedX - 100, clickedY - 100);
            this.jumpTargetTo(clickedX, clickedY);

        });

        $('.map-canvas').on('scroll', (e) => {

            $('.route-canvas').css({
                left: ($('.map-canvas').scrollLeft() * -1) - 80,
                top: ($('.map-canvas').scrollTop() * -1) + 35,
            });

            $('.target').css({
                left: ($('.map-canvas').scrollLeft() * -1) + this.lastTargetX - 95,
                top: ($('.map-canvas').scrollTop() * -1) + this.lastTargetY
            });

        });

        setTimeout(() => {
            this.drawBestRouterTo(958, 330);
            this.centerMapTo(958 - $(document).width() / 2, 330 - $(document).height() / 2);
        }, 1500);

    }

    private drawBestRouterTo = (toX, toY) => {

        this.ctx.beginPath();
        this.ctx.clearRect(0, 0, this.canvasEl.width, this.canvasEl.height);

        let result;

        if (this.lastTargetX == 0 && this.lastTargetY == 0) {
            result = Store.syncDrawBestWay(this.lastTargetX, this.lastTargetY, toX, toY);
        } else {
            result = Store.syncDrawBestWay(this.lastTargetX - 100, this.lastTargetY - 100, toX, toY);
        }

        for (var i = 0; i < result.length; i++) {
            this.syncMoveTargetTo(result[i].x * Store.aStarScale + 100, result[i].y * Store.aStarScale + 100);
        }

        this.drawPoint(toX + 85, toY + 5, '#ff0000');
    }


    private onSearchInput = (e) => {
        this._searchValue = e.target.value;
    }


    private onSearchInputKey = (e) => {
        if (e.key === 'Enter') {
            this.context.router.push('/dashboard/find?q=' + this._searchValue);
        }
    }

    private lastTargetX = 0;
    private lastTargetY = 0;

    private jumpTargetTo(x: number, y: number, then?) {
        this.drawPoint(x, y - 90, '#00FF00');
        this.centerMapTo(x - $(document).width() / 2, y - $(document).height() / 2);
    }


    private moveTargetTo(x: number, y: number, then?) {
        this.drawBestRouterTo(x, y);

        $('.target').css({left: x, top: y});

        this.centerMapTo(x - $(document).width() / 2, y - $(document).height() / 2);
    }


    private drawPoint = (x, y, color) => {
        this.ctx.strokeStyle = color;
        this.ctx.beginPath();
        this.ctx.arc(x, y, 10, 0, 2 * Math.PI);
        this.ctx.stroke();
    }

    private drawText = (x, y, color, text) => {
        this.ctx.font = "20px Arial";
        this.ctx.fillText(text, x, y);
    }

    private drawImage = (x, y, url) => {
        let img = new Image;
        img.onload = () => {
            this.ctx.drawImage(img, x, y,80,80);
        };
        img.src = url;
    }

    private syncMoveTargetTo(x: number, y: number) {
        this.drawLineSync(this.lastTargetX, this.lastTargetY, x, y);
        this.lastTargetX = x;
        this.lastTargetY = y;
    }

    private canvasEl;
    private ctx;

    private drawLineSync = (fromX, fromY, toX, toY) => {
        fromX -= 5;
        fromY -= 90;

        toX -= 5;
        toY -= 90;

        this.ctx.strokeStyle = '#ff0000';
        this.ctx.moveTo(fromX, fromY);
        this.ctx.lineTo(toX, toY);
        this.ctx.stroke();
    }


    private drawLine = (fromX, fromY, toX, toY, then?) => {

        fromY -= 90;
        toY -= 90;
        fromX -= 5;
        toX -= 5;

        this.ctx.strokeStyle = '#ff0000';

        var amount = 0;
        var iter = setInterval(() => {
            amount += 0.05;
            if (amount > 1) {
                this.drawPoint(toX, toY, '#ff0000');
                clearInterval(iter);
                if (then)
                    then();
            }

            this.ctx.moveTo(fromX, fromY);
            this.ctx.lineTo(fromX + (toX - fromX) * amount, fromY + (toY - fromY) * amount);
            this.ctx.stroke();
        }, 25);
    }

    private centerMapTo(x: number, y: number) {

        $('.map-canvas').animate({
            scrollLeft: x,
            scrollTop: y
        }, 500);
    }

    private navigateToNextProduct = () => {

        if (Store.productsToBuy.length == 0) {
            alert('no more item in your cart ...');
            return;
        }

        let nextProduct = Store.productsToBuy[0];
        Store.productsToBuy.shift();

        this.moveTargetTo(nextProduct.location.x, nextProduct.location.y);
        this.drawText(nextProduct.location.x, nextProduct.location.y, '#ff0000', nextProduct.name);
        this.drawImage(nextProduct.location.x, nextProduct.location.y, nextProduct.image);


        this._searchValue = nextProduct.name;
        this.forceUpdate();
    }


    render() {
        return (
            <div className="navigator-screen">


                <div className="input-group">
                    <input type="text" className="form-control"
                           onChange={this.onSearchInput}
                           value={this._searchValue}
                           onKeyDown={this.onSearchInputKey}/>

                    <span className="input-group-addon"
                          onClick={() =>{this.context.router.push('/dashboard/find?q='  +this._searchValue)}}>
                        <span className="glyphicon glyphicon-search"/>
                    </span>


                    <div className="dropdown input-group-addon">
                    <span className="dropdown-toggle" id="dropdownMenu1"
                          data-toggle="dropdown" aria-haspopup="true" aria-expanded="true">
                         ({Store.productsToBuy.length})
                    </span>

                        <ul className="dropdown-menu" aria-labelledby="dropdownMenu1">
                            {Store.productsToBuy.map((item: Item) => {
                                return <li key={'li_'+  item.name}>
                                    <a href="#">{item.name}</a>
                                </li>
                                //     <li role="separator" className="divider"></li>
                            })}
                        </ul>
                    </div>

                    <span className="input-group-addon" onClick={this.navigateToNextProduct}>
                        <span className="glyphicon glyphicon-forward"/>
                    </span>
                </div>

                <div className="map-canvas" style={{'overflow': 'scroll' , height: '100%'}}>
                    <img className="target free" src={this.targetImgSrc}/>
                    <canvas className="route-canvas" height={Store.mapSize.y} width={Store.mapSize.x}></canvas>
                    <img src={Store.mapFile}/>
                </div>
            </div>
        );
    }
}


