import * as React from "react";
import {IRouter} from "react-router";
import {IRouterContext} from "../dataTypes/IRouterContext";


export default class Application extends React.Component<any, any> {

    public static router: IRouter;

    context: IRouterContext;

    componentWillMount() {
        Application.router = this.context.router;
    }

    render() {
        return React.Children.only(this.props.children);
    }
}



