import * as React from "react";
import {Store} from "../../store/store";
import _ = require("lodash");

declare let $;

// aStar algo types :
declare let Graph;
declare let astar;

export default class Admin extends React.Component<any, any> {

    componentDidMount() {

        $('.map-canvas').on('mousemove', (e) => {

            var clickedX = ($('.map-canvas').scrollLeft()) + e.pageX - 15;
            var clickedY = ($('.map-canvas').scrollTop() ) + e.pageY - 81;

            if (e.ctrlKey) {
                this.fillRect(clickedX, clickedY);
            } else if (e.altKey) {
                this.clearRect(clickedX, clickedY);
            }
        });

        $('.map-canvas').on('click', (e) => {
            var clickedX = ($('.map-canvas').scrollLeft()) + e.pageX - 15;
            var clickedY = ($('.map-canvas').scrollTop() ) + e.pageY - 81;

            //console.log(clickedX, clickedY);

            this.fillRect(clickedX, clickedY);
        });

        $('.map-canvas').on('scroll', (e) => {

            $('#route-canvas').css({
                left: ($('.map-canvas').scrollLeft() * -1),// - 80,
                top: ($('.map-canvas').scrollTop() * -1)// + 35,
            });

        });


        // this.drawLine(0, 0, 250, 250);
        // this.syncDrawBestWay(400, 600);
        // this.syncDrawBestWay(580, 600);

        this.drawGrid();

        console.log(Store.obstacles);

        for (var i = 0; i < Store.obstacles.length; i++) {
            this.fillRect(Store.obstacles[i].x * Store.aStarScale, Store.obstacles[i].y * Store.aStarScale);

            this.marked.push({
                x: Store.obstacles[i].x * Store.aStarScale,
                y: Store.obstacles[i].y * Store.aStarScale});

        }
    }

    drawGrid = () => {

        let c = document.getElementById("route-canvas");
        let ctx = c.getContext("2d");
        ctx.strokeStyle = '#000000';

        for (let i = 0; i < Store.mapSize.y; i += Store.aStarScale) {
            ctx.moveTo(0, i);
            ctx.lineTo(Store.mapSize.x, i);
        }

        for (let i = 0; i < Store.mapSize.x; i += Store.aStarScale) {
            ctx.moveTo(i, 0);
            ctx.lineTo(i, Store.mapSize.y);
        }
        ctx.stroke();
    }

    private marked = [];

    clearRect = (x, y) => {
        let c = document.getElementById("route-canvas");
        let ctx = c.getContext("2d");
        ctx.fillStyle = "rgba(0, 0, 0, 0)";

        var point = {
            x: x - x % Store.aStarScale,
            y: y - y % Store.aStarScale
        }

        this.marked = this.marked.filter(p => {
            return p.x != point.x || p.y != point.y;
        });

        ctx.clearRect(point.x, point.y, Store.aStarScale, Store.aStarScale);

        ctx.strokeStyle = '#000000';
        ctx.rect(point.x, point.y, Store.aStarScale, Store.aStarScale);
        ctx.stroke();
    }

    fillRect = (x, y) => {
        let c = document.getElementById("route-canvas");
        let ctx = c.getContext("2d");
        ctx.fillStyle = "rgba(255, 0, 0, 0.5)";

        var point = {
            x: x - x % Store.aStarScale,
            y: y - y % Store.aStarScale
        }

        var isFound = this.marked.filter(p => {
            return p.x == point.x && p.y == point.y;
        });

        if (isFound.length == 0) {
            this.marked.push(point);
            ctx.fillRect(point.x, point.y, Store.aStarScale, Store.aStarScale);
        }
    }

    private drawPoint = (x, y, color) => {

        let c = document.getElementById("route-canvas");
        let ctx = c.getContext("2d");
        ctx.strokeStyle = color;

        ctx.beginPath();
        ctx.arc(x, y, 10, 0, 2 * Math.PI);
        ctx.stroke();
    }

    private exportAdminData = () => {

        console.log(this.marked);
        var obs2send = [];
        for (var i = 0; i < this.marked.length; i++) {
            obs2send.push({x :Math.round(this.marked[i].x / Store.aStarScale) , y: Math.round(this.marked[i].y / Store.aStarScale)});
        }

        Store.updateObstaclesPositions(obs2send);

    }

    private scaleChanged = (e) => {

        let c = document.getElementById("route-canvas");
        let ctx = c.getContext("2d");
        ctx.beginPath();
        ctx.clearRect(0, 0, c.width, c.height);

        Store.aStarScale = parseInt(e.target.value);

        this.drawGrid();
        this.forceUpdate();
    }

    render() {
        return (
            <div className="admin-screen">

                <div className="map-canvas" style={{'overflow': 'scroll' , height: '100%'}}>
                    <canvas id="route-canvas" height={Store.mapSize.y} width={Store.mapSize.x}></canvas>
                    <img
                        src="http://images.adsttc.com/media/images/530f/3b71/c07a/802c/7600/017f/large_jpg/plans-use-A3-02.jpg?1393507153"/>
                </div>

                <button className="export" onClick={this.exportAdminData}>Export</button>
                <input className="aStarScaleRange" value={Store.aStarScale.toString()} type="range" name="points"
                       min="5" max="100" onChange={this.scaleChanged}/>
            </div>
        );
    }
}


