import "babel-polyfill";
import "whatwg-fetch";
import * as React from "react";
import * as ReactDOM from "react-dom";
import {Router, Route, Redirect, hashHistory} from "react-router";
import Application from "./components/Application";
import AppFrame from "./components/appFrame/appFrame";
import Deals from "./components/deals/deals";
import Navigator from "./components/navigator/navigator";
import Welcome from "./components/welcome/welcome";
import Find from "./components/find/find";
import Admin from "./components/admin/admin";

declare let require;
declare let window;

// This will force the webpack to inject the <style> to this file, because now it knows it dependent on css file
require("./app.scss");

let renderApplication = () => {
    ReactDOM.render(

        <div className="react-root">
            <Router history={hashHistory}>
                <Redirect from="/" to="/dashboard/welcome"/>
                <Route path="/" component={Application}>

                    <Route path="dashboard" component={AppFrame}>
                        <Route path="welcome" component={Welcome}/>
                        <Route path="find" component={Find}/>
                        <Route path="navigator" component={Navigator}/>
                        <Route path="deals" component={Deals}/>

                        <Route path="admin" component={Admin}/>
                    </Route>
                </Route>
            </Router>
        </div>, document.getElementById("app")
    );
};

//                   <Route path="*" component={NotFound}/>

renderApplication();



