    public static productsToBuy = [];

    public static mapSize = {x: 2000, y: 2800};
    public static aStarScale;

    private static _graphData = [];
    private static _graph;

    public static syncDrawBestWay = (formX, fromY, toX, toY) => {

        var start = Store._graph.grid[Math.floor(formX / Store.aStarScale)][Math.floor(fromY / Store.aStarScale)];
        var end = Store._graph.grid[Math.floor(toX / Store.aStarScale)][Math.floor(toY / Store.aStarScale)];
        var result = astar.search(Store._graph, start, end);

        return result;
    }

    private static resetGrid() {
        Store._graphData = [];

        for (let i = 0; i < 2800 / Store.aStarScale; i++) {
            let row = [];
            for (let j = 0; j < 2000 / Store.aStarScale; j++) {
                row.push(1);
            }
            Store._graphData.push(row)
        }

        Store._graph = new Graph(Store._graphData);
    }

    public static initialize() {

        try {
            Store.obstacles = JSON.parse(localStorage.getItem('obstacles'));
            console.log("loaded " , Store.obstacles);
        } catch (ex) {
            Store.obstacles = [];
        }

        Store.aStarScale = parseInt(localStorage.getItem('aStarScale'));

        if (isNaN(Store.aStarScale)) {
            Store.aStarScale = 25;
        }

        Store.resetGrid();
        if (!Store.obstacles || !Store.obstacles.length) {
            Store.obstacles = [];
        }else{
            this.updateObstaclesPositions(Store.obstacles);
        }
    }

    public static obstacles;

    public static updateObstaclesPositions = (obstacles) => {

        Store.obstacles = obstacles;

        localStorage.setItem('obstacles', JSON.stringify(obstacles));
        localStorage.setItem('aStarScale', Store.aStarScale);

        console.log("~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~");
        console.log("localStorage.setItem('obstacles', JSON.stringify("+JSON.stringify(obstacles)+"));");
        console.log("localStorage.setItem('aStarScale', "+Store.aStarScale+");");
        console.log("~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~");

        Store.resetGrid();

        for (let i = 0; i < obstacles.length; i++) {
            Store._graphData[obstacles[i].x][obstacles[i].y] = 0;
        }

        Store._graph = new Graph(Store._graphData);
    }

