import * as React from "react";
import {IRouterContext} from "../../dataTypes/IRouterContext";
import {Store, Item} from "../../store/store";
import _ = require("lodash");
declare let $;

interface IFindProps {
    location: any;
}

export default class Find extends React.Component<IFindProps, any> {


    static contextTypes = {
        router: React.PropTypes.any,
    };

    context: IRouterContext;

    private _items = Store.products;

    private _searchValue: string = '';

    componentWillMount() {
        this._searchValue = this.props.location.query['q'];
    }

    private onSearchInput = (e) => {
        this._searchValue = e.target.value;
        this.forceUpdate();
    }

    private showOnMap = (item: Item) => {
        this.context.router.push('dashboard/navigator?q=' + item.name);
    }

    private addToCart = (item: Item) => {
        Store.productsToBuy.push(item);
        //alert('Item added ! ');
    }

    render() {
        return (
            <div className="find-page">
                <div className="input-group">
                    <span className="input-group-addon" id="basic-addon1">Find : </span>

                    <input type="text" className="form-control"
                           onChange={this.onSearchInput}
                           defaultValue={this._searchValue}/>

                    <span className="input-group-addon"
                          onClick={() =>{}}>
                        <span className="glyphicon glyphicon-search"/>
                    </span>
                </div>


                <table className="mui-table mui-table--bordered">
                    <thead>
                    <tr>
                        <th>Item Picture</th>
                        <th>Item Name</th>
                        <th>Item Category</th>
                        <th>Item Price</th>
                        <th>Add</th>
                        <th>Go</th>
                    </tr>
                    </thead>

                    <tbody>

                    {this._items.filter((item) => {
                        return !this._searchValue ||
                            item.name.toLocaleLowerCase().indexOf(this._searchValue.toLocaleLowerCase()) != -1 ||
                            item.category.toLocaleLowerCase().indexOf(this._searchValue.toLocaleLowerCase()) != -1
                    }).map((item) => {

                        return <tr>
                            <td><img className="item-image" src={item.image}/></td>
                            <td>{item.name}</td>
                            <td>{item.category}</td>
                            <td>{item.price}</td>

                            <td onClick={()=>{this.addToCart(item)}}>
                                <span className="glyphicon glyphicon-download-alt addToCart"></span>
                            </td>

                            <td onClick={()=>{this.showOnMap(item)}}>
                                <span className="glyphicon glyphicon-play-circle marked"></span>
                            </td>
                        </tr>

                    })}

                    </tbody>
                </table>


            </div>
        );
    }
}



